












































































import axios from "axios"
import { mapState, mapMutations, mapGetters, mapActions } from "vuex"
export default {
  name: "FormDish",
  data() {
    return {
      mealList: [],
      optionsMeals: [
        { value: 1, text: "Breakfest" },
        { value: 2, text: "Lunch" },
        { value: 3, text: "Dinner" },
        { value: 4, text: "Supper" },
      ],
      form: {
        mealId: null,
        date: this.mealTarget.date,
        userId: this.$route.params.id,
        meal: this.mealTarget.meal,
        name: null,
        dishId: null,
        portion: null,
        kcal: null,
        protein: null,
        carbo: null,
        fat: null,
      },
    }
  },
  computed: {
    ...mapState(["usersData"]),
  },
  methods: {
    ...mapMutations(["SAVE_DATA"]),
    loadData() {
      axios({
        method: "get",
        url: "https://fitvueapi.azurewebsites.net/dishes",
      })
        .then(response => {
          this.dishesList = [...response.data]
          //this.saveData()
          //localStorage.setItem("storedData", this.usersList)
        })
        .catch(err => {
          // Manage the state of the application if the request
          // has failed
        })
    },
    onSubmit: function() {
      let link = "https://fitvueapi.azurewebsites.net/meals/"
      axios
        .post(link, this.form)
        .then(function(response) {
          console.log(response)
        })
        .catch(function(error) {
          console.log(error)
        })
      //setTimeout(() => this.$emit("clicked", "someValue"), 350)
    },
    checkFormValidity() {
      //const valid = this.$refs.form.checkValidity()
      //this.nameState = valid
      //return valid
    },
    handleOk(bvModalEvt) {
      // Prevent modal from closing
      bvModalEvt.preventDefault()
      // Trigger submit handler
      this.handleSubmit()
    },
    handleSubmit() {
      // Exit when the form isn't valid
      /*       if (!this.checkFormValidity()) {
        return
      } */
      // Push the name to submitted names
      this.onSubmit(),
        //        this.submittedNames.push(this.name)
        // Hide the modal manually
        this.$nextTick(() => {
          this.$bvModal.hide("modal-prevent-closing")
        })
    },
  },
  mounted() {
    //this.loadDishesList()
    this.loadData()
  },
  props: {
    mealTarget: {
      type: Object,
    },
  },
}
