










































import axios from "axios"
import { mapState, mapMutations, mapGetters, mapActions } from "vuex"
import firstDish from "./firstDish"
export default {
  name: "FormDish",
  data() {
    return {
      dishesList: [],
    }
  },
  methods: {
    onSubmit: function() {
      let idek = this.$route.params.id
      console.log(this.dishData._id)
      if (idek == null) {
        let link = "https://fitvueapi.azurewebsites.net/dishes/"
        axios
          .post(link, this.dishData)
          .then(function(response) {
            console.log(response)
          })
          .catch(function(error) {
            console.log(error)
          })
      } else {
        let link = "https://fitvueapi.azurewebsites.net/dishes/" + idek
        axios
          .put(link, this.dishData)
          .then(function(response) {
            console.log(response)
          })
          .catch(function(error) {
            console.log(error)
          })
      }
      setTimeout(() => this.$emit("clicked", "someValue"), 350)
    },
    onExample() {},
  },
  props: {
    dishData: {
      default: () => firstDish,
      type: Array,
    },
  },
}
