






















import axios from "axios"
import router from "vue-router"
import FormMeal from "@/components/FormMeal.vue"
import DietTable from "@/components/DietTable.vue"
import DatePick from "vue-date-pick"
import "vue-date-pick/dist/vueDatePick.css"
export default {
  name: "Diet",
  data: function() {
    return {
      dishesList: [],
      meals: [
        { name: "Breakfest", id: 1 },
        { name: "Lunch", id: 2 },
        { name: "Dinner", id: 3 },
        { name: "Supper", id: 4 },
      ],
      fields: [
        {
          key: "name",
          label: "Meal",
        },
        { key: "actions", label: "Actions" },
      ],
      formModal: {
        id: "form-modal",
        title: "",
        content: "",
      },
      mealTarget: {
        meal: null,
        userId: this.$route.params.id,
        date: "2020-04-16",
      },
      //userID: this.$route.params.id,
    }
  },
  components: {
    FormMeal,
    DatePick,
    DietTable,
  },
  methods: {
    loadData() {
      axios({
        method: "get",
        url: "https://fitvueapi.azurewebsites.net/dishes",
      })
        .then(response => {
          this.dishesList = [...response.data]
          //console.log(this.dishesList)
          //this.saveData()]
        })
        .catch(err => {
          // Manage the state of the application if the request
          // has failed
        })
    },
    info(item, index, button) {
      //this.formModal.title = `Add ${item.name}`
      //console.log(item.name)
      this.mealTarget.meal = item.id
      //this.$root.$emit("bv::show::modal", this.formModal.id, button)
    },
  },
  mounted() {
    this.loadData() // Load Dishes
    //console.log(this.post.title)
  },
}
